import React, { createContext, useReducer } from 'react';
import actionTypes from '../constants/ActionTypes';

export const PatientContext = createContext();

let initialState = {
    patients: [],
    patient: {},
    isEdit: false,
    isLoading: true,
    isError: false,
    message: '',
};

const reducer = (state, action) => {
    console.log(action);
    switch (action.type) {
        case actionTypes.GET_PATIENTS: {
            return {
                ...state,
                isLoading: true,
            };
        }
        case actionTypes.GET_PATIENTS_SUCCESS: {
            return {
                ...state,
                isLoading: false,
                patients: action.payload,
            };
        }
        case actionTypes.GET_PATIENTS_ERROR: {
            return {
                ...state,
                isError: true,
                message: action.payload,
                isLoading: false,
            };
        }

        case actionTypes.SET_PATIENT_DETAILS: {
            return {
                ...state,
                isEdit: !!action.payload,
                patient: action.payload || {},
            };
        }

        default: {
            return state;
        }
    }
};

const PatientContextProvider = (props) => {
    const [patientsData, setPatientsData] = useReducer(reducer, initialState);

    return (
        <PatientContext.Provider value={{ patientsData, setPatientsData }}>
            {props.children}
        </PatientContext.Provider>
    );
};

export default PatientContextProvider;
