import React, { useContext, useState, useEffect } from 'react';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Link from '@material-ui/core/Link';
import Typography from '@material-ui/core/Typography';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import { makeStyles } from '@material-ui/core/styles';

import { PatientContext } from '../../store/PatientContext';
import ActionTypes from '../../constants/ActionTypes';
import TextInput from '../../components/TextInput';

function preventDefault(event) {
    event.preventDefault();
}

const useStyles = makeStyles((theme) => ({
    paper: {
        padding: theme.spacing(2),
        display: 'flex',
        overflow: 'auto',
        flexDirection: 'column',
    },
    fixedHeight: {
        height: 240,
    },
    actionButtons: {
        '& > button': {
            margin: theme.spacing(1),
        },
    },
    seeMore: {
        marginTop: 10,
    },
    tableHeader: {
        backgroundColor: '#e0e0e0',
        '& th': {
            borderLeft: '1px solid #ededed',
            padding: '5px 10px',
        },
    },
    tableBody: {
        '& td': {
            padding: '5px 10px',
        },
    },
}));

function Patients() {
    const classes = useStyles();
    const { patientsData, setPatientsData } = useContext(PatientContext);
    const { patients, isLoading, patient, isEdit } = patientsData;

    const [patientDetails, setPatientDetails] = useState(patient);

    useEffect(() => {
        setPatientDetails(patient);
    }, [patient]);

    const handleChange = (e) => {
        let { name, value } = e.target;
        setPatientDetails({
            ...patientDetails,
            [name]: value,
        });
    };
    const handleAddUpdate = () => {
        setPatientDetails({
            ...patientDetails,
            createdDate: patientDetails.createdDate
                ? patientDetails.createdDate
                : new Date().toISOString(),
            updatedDate: new Date().toISOString(),
        });
    };

    return (
        <>
            <Grid item xs={8}>
                <Paper className={classes.paper}>
                    <Typography
                        component="h2"
                        variant="h6"
                        color="primary"
                        gutterBottom
                    >
                        Patients List
                    </Typography>
                    {patients.length > 0 ? (
                        <Table size="small">
                            <TableHead className={classes.tableHeader}>
                                <TableRow>
                                    <TableCell>Date</TableCell>
                                    <TableCell>Name</TableCell>
                                    <TableCell>Scan</TableCell>
                                    <TableCell>Referred By</TableCell>
                                    <TableCell align="right">Amount</TableCell>
                                    <TableCell align="right">Action</TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody className={classes.tableBody}>
                                {patients.map((row) => (
                                    <TableRow key={row.id}>
                                        <TableCell>{row.createdDate}</TableCell>
                                        <TableCell>{row.name}</TableCell>
                                        <TableCell>{row.scan}</TableCell>
                                        <TableCell>
                                            {row.referredBy || row.referedBy}
                                        </TableCell>
                                        <TableCell align="right">
                                            {row.amount}
                                        </TableCell>
                                        <TableCell align="right">
                                            <Button
                                                size="small"
                                                color="primary"
                                                onClick={() => {
                                                    setPatientsData({
                                                        type: ActionTypes.SET_PATIENT_DETAILS,
                                                        payload: row,
                                                    });
                                                }}
                                            >
                                                Edit
                                            </Button>
                                            <Button
                                                size="small"
                                                color="primary"
                                                onClick={() => {
                                                    setPatientsData({
                                                        type: ActionTypes.SET_PATIENT_DETAILS,
                                                        payload: row,
                                                    });
                                                }}
                                            >
                                                Delete
                                            </Button>
                                        </TableCell>
                                    </TableRow>
                                ))}
                            </TableBody>
                        </Table>
                    ) : (
                        <div className="zero-state">No Data</div>
                    )}
                    {patients.length > 0 && (
                        <div className={classes.seeMore}>
                            <Link
                                color="primary"
                                href="#"
                                onClick={preventDefault}
                            >
                                See more
                            </Link>
                        </div>
                    )}
                </Paper>
            </Grid>
            <Grid item xs={4}>
                <Paper className={classes.paper}>
                    <Typography
                        component="h2"
                        variant="h6"
                        color="primary"
                        gutterBottom
                    >
                        Add Patient
                    </Typography>
                    <form
                        className={classes.form}
                        noValidate
                        autoComplete="off"
                    >
                        <Grid spacing={2} container>
                            <Grid item xs={12}>
                                <TextInput
                                    variant="outlined"
                                    required
                                    fullWidth
                                    id="name"
                                    label="Patient Name"
                                    name="name"
                                    size="small"
                                    value={patientDetails.name}
                                    onChange={handleChange}
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <TextInput
                                    variant="outlined"
                                    required
                                    fullWidth
                                    id="Scan"
                                    label="Scan"
                                    name="scan"
                                    size="small"
                                    value={patientDetails.scan}
                                    onChange={handleChange}
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <TextInput
                                    variant="outlined"
                                    required
                                    fullWidth
                                    id="amount"
                                    label="Amount"
                                    name="amount"
                                    type="number"
                                    size="small"
                                    value={patientDetails.amount}
                                    onChange={handleChange}
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <TextInput
                                    variant="outlined"
                                    required
                                    fullWidth
                                    id="referral"
                                    label="Referred By"
                                    name="referralBy"
                                    size="small"
                                    value={patientDetails.referredBy}
                                    onChange={handleChange}
                                />
                            </Grid>
                            <Grid
                                item
                                xs={12}
                                className={classes.actionButtons}
                            >
                                <Button
                                    variant="contained"
                                    color="primary"
                                    onClick={handleAddUpdate}
                                >
                                    {isEdit ? 'Update' : 'Add'}
                                </Button>
                                <Button
                                    variant="contained"
                                    onClick={() => {
                                        setPatientDetails({});
                                        setPatientsData({
                                            type: ActionTypes.SET_PATIENT_DETAILS,
                                            payload: null,
                                        });
                                    }}
                                >
                                    Clear
                                </Button>
                            </Grid>
                        </Grid>
                    </form>
                </Paper>
            </Grid>
        </>
    );
}

export default Patients;
