import React from 'react';

import TextField from '@material-ui/core/TextField';

function TextInput(props) {
    if (!props.value) {
        return <TextField {...props} value="" />;
    }
    return (
        <TextField
            {...props}
            InputLabelProps={{
                shrink: true,
            }}
        />
    );
}

export default TextInput;
